@use 'util/variables' as *;

.header__main {
	position: sticky;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	box-shadow: $box-shadow;
	padding: 0.7rem 5%;
	z-index: 1000;
	transition: all 0.25s ease;
	@media (min-width: $breakpoint-desktop) {
		padding: 0 5%;
		gap: 0 1%;
		&.scrolled {
			padding: 0 5% !important;
		}
	}
	&.scrolled,
	&.focus {
		padding: 0.5rem 5%;
		background-color: $blue-gray-900;
	}
	.logo {
		display: flex;
		align-items: center;
		max-width: fit-content;
		&__text {
			font-size: 1.3rem;
			margin-inline: 0.5rem;

			@media (min-width: $breakpoint-desktop) {
				font-size: 1.5rem;
			}
		}
	}
	.line {
		display: none;
		@media (min-width: $breakpoint-desktop) {
			display: block;
			height: 2rem;
			border: 0.08rem solid;
			border-color: $blue-gray-400;
			background-color: $blue-gray-400;
			margin-left: 1%;
		}
	}
	.left__side-mobile {
		display: flex;
		align-items: center;
		gap: 1rem;
		.btn {
			padding: 0;
			.profile__icon-mobile {
				padding: 0;
				height: 2rem;
				opacity: 0.85;
			}

			@media (min-width: $breakpoint-desktop) {
				display: none;
			}
			&-balance {
				display: none;
				@media (min-width: $breakpoint-tablet) {
					display: block;
				}
			}
		}

		.mobile__burger {
			position: relative;
			cursor: pointer;
			padding: 0.65rem 1rem;
			opacity: 0.8;
			@media (min-width: $breakpoint-desktop) {
				display: none;
			}
			&.open {
				opacity: 1;
				.mobile__burger_line {
					&:nth-child(1) {
						transform: rotate(45deg);
						top: 0.55rem;
					}
					&:nth-child(2) {
						opacity: 0;
					}
					&:nth-child(3) {
						transform: rotate(-45deg);
						bottom: 0.55rem;
					}
				}
			}
			&_line {
				position: absolute;
				right: 0;
				display: block;
				width: 1.5rem;
				padding: 0.1rem 0;
				border-radius: 0.5rem;
				transition: all 0.25s ease;
				background-color: $blue-gray-200;
				&:nth-child(1) {
					top: 0;
				}
				&:nth-child(2) {
					top: 0.55rem;
				}
				&:nth-child(3) {
					bottom: 0;
				}
			}
		}
	}

	.mobile {
		display: none;
		position: fixed;
		top: 3.4rem;
		left: 0;
		width: 100%;
		height: calc(100% - 3rem);
		background-color: $blue-gray-800;
		@media (min-width: $breakpoint-desktop) {
			position: static;
			display: block;
			background-color: transparent;
		}
		&.open {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			overflow-y: auto;
			animation: slide 0.3s ease-in-out;

			@keyframes slide {
				0% {
					transform: translateX(100%);
				}
				100% {
					transform: translateX(0);
				}
			}
		}
		&__menu {
			display: flex;
			flex-direction: column;
			@media (min-width: $breakpoint-desktop) {
				flex-direction: row;
				align-items: center;
				gap: 0.5rem;
			}
			&_search {
				display: flex;
				align-items: center;
				position: relative;
				@media (min-width: $breakpoint-desktop) {
					order: 2;
					width: 50%;
					max-width: 35rem;
				}
				.search__icon {
					position: absolute;
					left: 0.8rem;
					img {
						width: 90%;
					}
				}
				.search__input {
					width: 100%;
					font-size: 1rem;
					padding: 1rem 1rem 1rem 2.5rem;
					@media (min-width: $breakpoint-desktop) {
						height: 2.5rem;
					}
					&::-webkit-search-cancel-button {
						-webkit-appearance: none;
						cursor: pointer;
						height: 1.5rem;
						width: 1.5rem;
						background: url(../img/icons/xmark-solid.svg) no-repeat 50% 50%;
						background-size: contain;
						opacity: 0;
						pointer-events: none;
					}
					&:focus::-webkit-search-cancel-button {
						opacity: 1;
						pointer-events: all;
					}
				}
			}
			.navbar {
				display: flex;
				flex-direction: column;
				margin: 5%;
				gap: 1rem 0;
				@media (min-width: $breakpoint-desktop) {
					order: 1;
					flex-direction: row;
					align-items: center;
					margin: 0;
					gap: 0 4%;
					width: 100%;
				}
				&__resources {
					@media (min-width: $breakpoint-desktop) {
						&:hover .dropdown__content {
							display: block;
						}
						&:hover .dropdown_menu {
							color: $blue-gray-50;
						}
					}
					.nav__btn {
						display: flex;
						align-items: center;
						gap: 0.5rem 1rem;
						font-size: 1.2rem;
						padding: 0.75rem 0.5rem;
						@media (min-width: $breakpoint-desktop) {
							font-size: 1.05rem;
							font-weight: 600;
							letter-spacing: 0.03rem;
							padding: 0 0.5rem;
							height: 4rem;
							gap: 0;
							color: $blue-gray-400;
							transition: height 0.25s ease;
							&.scrolled {
								height: 3.5rem;
							}
							&:hover {
								color: $blue-gray-50;
							}
						}
						.link__icon {
							width: 1.5rem;
							&.arrow__down {
								width: 0.8rem;
							}
							@media (min-width: $breakpoint-desktop) {
								display: none;
							}
						}
						&.dropdown_menu {
							cursor: pointer;
							@media (min-width: $breakpoint-desktop) {
								cursor: default;
							}
						}
					}
					.dropdown__content {
						display: none;
						@media (min-width: $breakpoint-desktop) {
							position: absolute;
							background-color: $blue-gray-900;
							box-shadow: $box-shadow;
							border-bottom-right-radius: 0.5rem;
							border-bottom-left-radius: 0.5rem;
							z-index: 100;
							width: 10rem;
							animation: growDown 0.2s ease-in-out;
							transform-origin: top center;

							@keyframes growDown {
								0% {
									transform: scaleY(0);
								}
								100% {
									transform: scaleY(1);
								}
							}
						}
						&.open {
							display: block;
							animation: growDown 0.25s ease;
							transform-origin: top center;
							@media (min-width: $breakpoint-desktop) {
								display: none;
							}
							@keyframes growDown {
								0% {
									transform: scaleY(0);
								}
								100% {
									transform: scaleY(1);
								}
							}
						}
						a {
							padding: 0.5rem 3rem;
							display: block;
							font-size: 1.2rem;
							width: fit-content;
							@media (min-width: $breakpoint-desktop) {
								font-size: 1rem;
								font-weight: 600;
								padding: 1rem 0.5rem;
								width: 100%;
								color: $blue-gray-400;
								transition: color 0.2s ease;
								&:nth-child(2) {
									border-block: 1px solid;
									border-color: $blue-gray-800;
								}
								&:last-child {
									border-radius: 0 0 0.5rem 0.5rem;
								}
								&:hover {
									box-shadow: $box-shadow-top;
									color: $blue-gray-50;
									background-color: $blue-gray-800;
								}
							}
						}
					}
				}
			}
			.btn {
				order: 3;
				display: none;
				@media (min-width: $breakpoint-desktop) {
					display: flex;
					padding: 0.4rem 0.5rem;
					&-profile {
						padding: 0;
						.profile__icon {
							width: 2rem;
							opacity: 0.5;
							transition: opacity 0.2s ease;
							&:hover {
								opacity: 1;
							}
						}
					}
				}
				&-balance {
					position: absolute;
					display: flex;
					gap: 0.5rem;
					width: fit-content;
					align-self: center;
					border-width: 2px;
					border-style: solid;
					border-color: $blue-gray-600;
					border-radius: 0.5rem;
					bottom: 11.5rem;
					padding: 0.5rem 1rem;

					@media (min-width: $breakpoint-desktop) {
						position: static;
						padding: 0.3rem 0.5rem;
						gap: 0;
						border-width: 1px;
						color: $blue-gray-400;
						transition: color 0.2s ease, border-color 0.2s ease;

						&:hover {
							color: $blue-gray-50;
							border-color: $blue-gray-400;
						}
						.wallet__icon {
							display: none;
						}
					}
				}
			}
		}
		&__footer {
			display: flex;
			justify-content: center;
			flex-direction: column;
			width: 100%;
			gap: 1rem 0;
			@media (min-width: $breakpoint-desktop) {
				display: none;
			}
			.btn__blue {
				margin-inline: 5%;
				padding-block: 0.5rem;
				font-size: 1.2rem;
				font-weight: 600;

				@media (min-width: $breakpoint-tablet) {
					align-self: center;
					width: 25rem;
				}
			}
			&_bg {
				display: flex;
				justify-content: center;
				background-color: $blue-gray-900;
				.logo__icons {
					padding-block: 2rem;
					width: 12rem;
					.icon {
						fill: $blue-gray-400;
						path {
							fill: $blue-gray-900;
						}
						&:hover {
							fill: $indigo;
							path {
								fill: $blue-gray-200;
							}
						}
					}
				}
			}
		}
	}
}
