::-webkit-scrollbar {
  width: 0.5rem;
  display: none;
}
@media (min-width: 1024px) {
  ::-webkit-scrollbar {
    display: block;
  }
}

::-webkit-scrollbar-thumb {
  background: hsl(217deg, 33%, 17%);
  border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
  background: hsla(225deg, 20%, 61%, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  list-style: none;
  color: hsl(210deg, 40%, 98%);
  background-color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Heebo", sans-serif;
  background-color: hsl(222deg, 46%, 11%);
  width: 100%;
}
body.scroll__lock {
  overflow: hidden;
}

img {
  display: block;
}

input {
  padding: 0.5rem;
  border: 1px solid;
  border-color: hsl(214deg, 20%, 34%);
  color: hsl(215deg, 20%, 65%);
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}
input:focus {
  color: hsl(210deg, 40%, 98%);
  background-color: hsl(217deg, 33%, 17%);
  border-color: hsl(215deg, 20%, 65%);
}
input:focus::placeholder {
  visibility: hidden;
}

.main__content {
  display: flex;
  flex-direction: column;
  margin-inline: 10%;
  gap: 5rem 0;
}

.wrapper {
  position: relative;
}

.wrapper__home .footer__main .container .subscribe,
.wrapper__home .footer__main .container .navigation {
  background-color: transparent;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

